.site-container {
  width: 1115px;
  margin: 0 auto;
}
@media (max-width: 1250px) {
  .site-container {
    width: 90%;
  }
}

.site-container h1 {
  color: white;
}

.navbar {
  cursor: pointer;
}
.nav-link:hover {
  background: #777;
}

span {
  color: #fff;
}

img {
  width: 60%;
}

.fh5co-top-banner {
  position: relative;
  display: flex;
  align-items: flex-end;
  background-image: url(../images/background.jpg);
  background-position: center;
  background-size: cover;
}

.fh5co-top-banner::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.9;
}

.fh5co-top-banner .site-container {
  position: relative;
  z-index: 1;
}

@media (max-width: 1600px) {
  .fh5co-top-banner {
    padding-top: 180px;
  }
}
@media (max-width: 1250px) {
  .fh5co-top-banner {
    padding-top: 130px;
  }
}
@media (max-width: 520px) {
  .fh5co-top-banner {
    padding-top: 105px;
  }
}
@media (min-width: 1600px) {
  .fh5co-top-banner {
    min-height: 100vh;
  }
}

.top-banner__inner {
  display: flex;
  align-items: center;
}
.top-banner__inner .top-banner__text {
  margin-left: 54px;
}
@media (max-width: 1250px) {
  .top-banner__inner .top-banner__text {
    width: 35%;
  }
}
@media (max-width: 768px) {
  .top-banner__inner .top-banner__text {
    width: 50%;
    margin-left: 20px;
  }
}
@media (max-width: 520px) {
  .top-banner__inner .top-banner__text {
    width: 65%;
    margin-bottom: 20px;
    margin-left: 0;
  }
}
.top-banner__inner .top-banner__text p {
  margin-bottom: 42px;
  color: #777;
}
@media (max-width: 768px) {
  .top-banner__inner .top-banner__text p {
    margin-bottom: 25px;
    font-size: 14px;
  }
}
.top-banner__inner .top-banner__h2 {
  font-size: 74px;
  letter-spacing: 3px;
  color: #fff;
  font-family: 'Calisto-MT-Bold';
}
@media (max-width: 1250px) {
  .top-banner__inner .top-banner__h2 {
    font-size: 50px;
  }
}
@media (max-width: 768px) {
  .top-banner__inner .top-banner__h2 {
    font-size: 35px;
  }
}
.top-banner__inner .top-banner__text-up .top-banner__h2 {
  margin-top: 15px;
}
@media (max-width: 768px) {
  .top-banner__inner .top-banner__text-up .top-banner__h2 {
    margin-top: 0;
  }
}
.top-banner__inner .top-banner__text-down {
  margin-bottom: 20px;
  padding-left: 27%;
}
.top-banner__inner .top-banner__text-down .top-banner__h2 {
  margin-bottom: 10px;
}
@media (max-width: 768px) {
  .top-banner__inner .top-banner__text-down .top-banner__h2 {
    margin-bottom: 0;
  }
}
.top-banner__inner .top-banner__image {
  font-size: 0;
}
@media (max-width: 1250px) {
  .top-banner__inner .top-banner__image {
    width: 50%;
  }
  .top-banner__inner .top-banner__image img {
    width: 100%;
  }
}

@media (max-width: 991px) {
  .top-banner__inner .top-banner__image {
    width: 80%;
  }
  .top-banner__inner .top-banner__image img {
    width: 90%;
  }
}
@media (max-width: 520px) {
  .top-banner__inner .top-banner__image {
    width: 65%;
    margin-bottom: 20px;
  }
}
@media (max-width: 520px) {
  .top-banner__inner {
    flex-direction: column;
  }
}

.brand-span {
  font-size: 14px;
  color: #c18f59;
}

.brand-button {
  display: inline-block;
  padding: 10px 20px;
  font-size: 11px;
  text-transform: uppercase;
  text-decoration: none;
  color: #c18f59;
  border: 1px solid #fff;
  transition: 0.3s ease;
}
.brand-button:hover,
.brand-button:focus {
  color: #777;
}
